















import { Component, Vue } from 'vue-property-decorator'

@Component({ name: 'PageLoading' })
export default class PageLoading extends Vue {
  loading = true

  beforeMount(): void {
    this.start()
  }

  mounted(): void {
    this.finish()
  }

  start(): void {
    this.loading = true
  }
  finish(): void {
    setTimeout(() => {
      this.loading = false
    }, 3000)
  }
}
